<template>
    <div class="Investment-and-incubation">
        <div class="investment-title">
            <span>{{ $LS("") }}</span>
        </div>
        <ul class="investment-ul">
            <li class="investment-lia">
                <img :src="images.icons" alt="" />
                <span>{{ $LS("") }}</span>
            </li>
            <li class="investment-lia">
                <img :src="images.icons4" alt="" />
                <span>{{ $LS("") }}</span>
            </li>
        </ul>
        <ul class="investment-list">
            <li class="investment-li">
                <img :src="images.icons2" alt="" />
                <span>{{ $LS("") }}</span>
            </li>
            <li class="investment-li">
                <img :src="images.icons1" alt="" />
                <span>{{ $LS("") }}</span>
            </li>
            <li class="investment-li">
                <img :src="images.icons5" alt="" />
                <span>{{ $LS("") }}</span>
            </li>
        </ul>
    </div>
</template>

<script>
export default {
    name: "InvestmentIncubation",
    data() {
        return {
            images: {
                icons: require("../../assets/small-icons.png"),
                icons1: require("../../assets/small-icons1.png"),
                icons2: require("../../assets/small-icons2.png"),
                icons3: require("../../assets/small-icons3.png"),
                icons4: require("../../assets/small-icons4.png"),
                icons5: require("../../assets/small-icons4.png"),
            },
        };
    },
};
</script>

<style lang="scss" scoped>
@import "../../styles/common";

@keyframes padding {
    from {
        padding-top: 500px;
    }

    to {
        padding-top: 0px;
    }
}
@keyframes paddingTop {
    from {
        padding-top: 400px;
    }

    to {
        padding-top: 132px;
    }
}

.Investment-and-incubation {
    height: 885px;
    /*   background-image: url("../../assets/group.png");
    background-size: 100% 640px; */
    background-color: #f4f4f4;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    animation: 1s padding;
    & span {
        font-weight: 400;
        color: #000000;
        animation: 2s opacity;
    }

    .investment-title {
        span {
            // font-size: 36px;
            font-size: 50px;
        }
    }
    .investment-ul {
        display: flex;
        justify-content: center;
        align-items: center;
        padding-top: 132px;
        animation: 1s paddingTop;
        // width: 550px;
        // margin: 0 50px;

        .investment-lia {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            animation: 2s opacity;
            // width: 320px;

            img {
                width: 54px;
                height: 54px;
            }

            span {
                padding-top: 44px;
                font-size: 24px;
            }
        }
        .investment-lia:nth-child(2){
            padding-left: 46px;
        }
    }

    .investment-list {
        display: flex;
        justify-content: center;
        align-items: center;
        // padding-top: 132px;
        padding-top: 100px;
        animation: 1s paddingTop;
        // width: 750px;

        .investment-li {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            //width: 250px;
            animation: 2s opacity;

            img {
                width: 54px;
                height: 54px;
            }

            span {
                padding-top: 44px;
                font-size: 22px;
            }
            // padding: 0 20px;
        }
        .investment-li:nth-child(2) {
            img {
                height: 47px;
            }
            span {
                padding-top: 54px;
            }
            padding: 0 46px;
        }
    }
}
</style>


