import { http, methods, HttpParams } from "./http";
export default {
    getCaseList() {
        let mark = 'case ';
        let method = methods.GET;
        let action = '/api/v1/case/list';
        // return http({ action, method, mark })
        return new HttpParams({ action, method, mark })
    },
    getInvestmentList() {
        let mark = 'case ';
        let method = methods.GET;
        let action = '/api/v1/investment/list';
        // return http({ action, method, mark })
        return new HttpParams({ action, method, mark })
    },
    testApi(params = { id }) {
        let mark = 'testApi';
        let method = methods.GET;
        let action = '/api/v1/test/hello';
        return http({ action, method, mark, params })
    },
}


