<template>
    <div class="Capital">
        <div class="Capital-div">
            <span class="Capital-name">
                {{ $LS("") }}
            </span>
        </div>

        <div class="Capital-top">
            <p class="Capital-text">
                {{
                    $LS(
                        "BFG CapitalBittrex2014BittrexBittrexBFG CapitalWeb 3.0",
