    min-height: 640px;
    max-height: 892px;
    background: #f4f4f4;
    border-radius: 12px;

    .Capital-div {
        text-align: center;
        padding-top: 69px;
        animation: 1s padding;
        .Capital-name {
            font-weight: 500;
