            animation: 2s opacity;
        }
    }

    .Capital-top {
        // width: 734px;
        width: 1063px;
        margin: auto;
        padding-top: 48px;
        animation: 1s paddingtop;

        .Capital-text {
            color: #616161;
            font-size: 32px;
            line-height: 42px;
            // text-indent: 2em;
            animation: 2s opacity;
        }

        .Capital-chart {
            text-align: center;
            padding: 100px 0 148px 0;
            .Capital-img {
