<template>
    <div class="dynamic">
        <ul class="dynamic-ul" :class="{ 'dynamic-active': menuIsShow }">
            <li class="dynamic-li" v-for="(item, index) in trendListData" :key="index">
                <div class="dynamic-li-left">
                    <img class="dynamic-pic" :src="item.imgUrl" alt=""/>
                    <span class="dynamic-name">{{ $LS(item.name) }}</span>
                </div>
            </li>
        </ul>
<!--        <div class="button" @click="menuShow">-->
<!--            <span>{{ $LS("") }}</span>-->
<!--        </div>-->
    </div>
</template>

<script>
import "../../common/px";
import { trendList } from "../items/dataConfig/coin.data";
import { arrayRemoveRepeat } from "../../util/data.util";

export default {
    name: "Dynamic",
    data() {
        return {
            menuIsShow: false,
        };
    },
    computed:{
        trendListData(){
            return arrayRemoveRepeat(trendList)
        }
    },
    methods: {
        menuShow() {
            this.menuIsShow = !this.menuIsShow;
        },
    },
};
</script>

<style lang="scss" scoped>
@import "../../styles/common";

@keyframes padding {
    from {
        padding-top: 300px;
    }
    to {
        padding-top: 78px;
    }
}

@mixin border-bottom {
    border-bottom: 1px solid #c8c8c8;
}

@mixin display-flex-ai {
    display: flex;
    align-items: center;
}

@mixin display-flex-wrap {
    flex-wrap: wrap;
}

@mixin display-flex-direction {
    @include display-flex-ai;
    flex-direction: column;
}

@mixin display-flex-ai-jc-sb {
    @include display-flex-ai;
    @include display-flex-wrap;
    justify-content: space-between;
}

.dynamic {
    width: 100%;
    background-color: #f4f4f4;
    color: #000000;

    .dynamic-ul {
        /* height: 210px;
        transition: height 0.8s ease;
        overflow: hidden; */
        margin: 0 47px;
        padding-top: 78px;
        @include display-flex-ai-jc-sb;
        animation: 1s padding;

        .dynamic-li {
            height: 178px;
            width: 320px;
            margin-bottom: 64px;
            @include border-bottom;
            animation: 2s opacity;

            .dynamic-li-left {
                @include display-flex-direction;

                .dynamic-pic {
                    width: 144px;
                    height: 72px;
                }

                .dynamic-name {
                    min-width: 100px;
                    max-width: 330px;
                    font-size: 28px;
                    color: #000;
                    padding: 20px 0 38px 0;
                    text-align: center;
                }
            }
        }
    }

    /* .dynamic-active {
        height: 3465px;
        transition: height 0.8s ease;
    }
 */
    .button {
        width: 220px;
        height: 38px;
        margin: auto;

        padding: 88px 0 122px 0;

        span {
            width: 100%;
            height: 38px;
            font-size: 28px;
            display: flex;
            justify-content: center;
            align-items: center;
            color: #000000;
            background: #ebebeb;
        }
    }

    // .button {
    //     width: 220px;
    //     height: 38px;
    //     margin: 0 auto;
    //     margin-top: 80px;
    //     padding: 88px 0 122px 0;
    //     border: 1px solid #d7d7d7;
    //     background: #ebebeb;
    //     border-radius: 4px;
    //     cursor: pointer;
    //     display: flex;
    //     justify-content: center;
    //     align-items: center;
    //     //display: none;
    //     span {
    //         font-size: 16px;
    //         color: #000000;
    //     }
    // }
}
</style>


