<template>
    <div>
        <Drawer title="" :closable="false" v-model="visible">
            <ul class="drawer-total">
                <li :class="{ 'active-link': homeActive }" @click="goToLink('home')">
                    <span>{{ $LS("") }}</span>
                    <Icon v-if="!homeActive" type="ios-arrow-forward" />
                </li>
                <li :class="{ 'active-link': aboutActive }" @click="goToLink('about')">
                    <span>{{ $LS("") }}</span>
                    <Icon v-if="!aboutActive" type="ios-arrow-forward" />
                </li>
                <li :class="{ 'active-link': developmentActive }" @click="goToLink('development')">
                    <span>{{ $LS("") }}</span>
                    <Icon v-if="!developmentActive" type="ios-arrow-forward" />
                </li>
                <li :class="{ 'active-link': enterpriseActive }" @click="goToLink('enterprise')">
                    <span>{{ $LS("") }}</span>
                    <Icon v-if="!enterpriseActive" type="ios-arrow-forward" />
                </li>
                <li :class="{ 'active-link': moreActive }" @click="goToLink('more')">
                    <span>{{ $LS("") }}</span>
                    <Icon v-if="!moreActive" type="ios-arrow-forward" />
                </li>
            </ul>
        </Drawer>
    </div>
</template>

<script>
import { nameUtil } from "../../router/util";
export default {
    props: {
        menuShowValue: null,
    },
    data() {
        return {
            visible: false,
            homeActive: false,
            aboutActive: false,
            developmentActive: false,
            enterpriseActive: false,
            moreActive: false,
            pathName: nameUtil.nameRemoveLanguage(this.$router.history.current.name),
        };
    },
    created() {
        switch (this.pathName) {
            case "home":
                this.homeActive = true;
                break;
            case "about":
                this.aboutActive = true;
                break;
            case "development":
                this.developmentActive = true;
                break;
            case "enterprise":
                this.enterpriseActive = true;
                break;
            case "more":
                this.moreActive = true;
                break;
        }
    },
    watch: {
        menuShowValue(nextProps) {
            this.visible = nextProps;
        },
    },
    methods: {
        goToLink(pathName) {
            this.$push({ name: pathName });
        },
    },
};
</script>

<style lang="scss" scoped>
/deep/ .ivu-drawer-body {
    background-color: #2c2c2c;
    .drawer-total {
        padding-top: 50px;
        font-size: 20px;
        color: #f2f2f2f2;
       
        li {
            height: 60px;
            font-size: 24px;
            cursor: pointer;
            line-height: 60px;
        }
    }
}
.active-link {
    span {
        font-size: 40px !important;
    }
}
</style>


