<template>
    <div class="more">
        <HeaderH5>
            <template v-slot:title>
                <div class="header-first" @click="upperLevel">
                    <img src="../assets/fork.png" alt="" class="header-fork" />
                </div>
            </template>
        </HeaderH5>
        <!-- <div class="more-icon">
            <img src="../assets/large-icon.png" alt="" class="more-img" />
        </div> -->
        <div class="more-title">
            <span class="more-text">{{ $LS("") }}</span>
            <!-- <span class="more-text" @click="jumpLink">{{ $LS("bp@bfg.capital") }}</span> -->
            <span class="more-text" @click="methodLaw('statute')">{{ $LS("") }}</span>
        </div>
        <div class="more-text-icon">
            <div class="more-chinese" @click="changeisShow">
                <span class="more-typeface">{{ langSelected.title }}</span>
                <div class="more-left-arrow">
                    <img src="../assets/left-arrow.png" alt="" class="more-Small-icon" />
                </div>
            </div>
            <div class="more-select-lang" v-show="isShow">
                <span
                    class="more-Britain-english"
                    v-for="(item, id) in languages"
                    :key="id"
                    v-show="langSelected.id !== item.id"
                    @click="languageSelect(item)"
                >
                    {{ item.title }}
                </span>
            </div>
        </div>
    </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import HeaderH5 from "./items/HeaderH5.vue";
import LANG_TYPE from "../localized/language_types";
import "../common/px";
import VueCookie from "vue-cookie";
export default {
    components: {
        HeaderH5,
    },
    data() {
        let languages = [
            { id: LANG_TYPE.ZH_HANS, title: "", short: "" },
            { id: LANG_TYPE.EN, title: "English", short: "English" },
        ];
        let langType = VueCookie.get("language") || LANG_TYPE.EN;
        let defaultLangSelected = languages.find(lang => {
            return lang.id === langType;
        });
        return {
            isShow: false,
            anchorIsShow: false,
            width: null,
            langSelected: defaultLangSelected,
            LANG_TYPE,
            languages,
        };
    },
    computed: {
        ...mapGetters("language", ["getLang"]),
        handleUpdateDimensions() {
            this.width = window.innerWidth;
        },
        currentLanguageId() {
            return this.$i18n.locale;
        },
        languagesCurrent() {
            return this.languages.find(lang => {
                return lang.id === this.currentLanguageId;
            });
        },
        languagesDropdown() {
            return this.languages.filter(lang => {
                return lang.id !== this.currentLanguageId;
            });
        },
    },
    methods: {
        ...mapActions("language", ["updateLang"]),
        methodLaw(pathName) {
            this.$push({ name: pathName });
        },
        upperLevel() {
            this.$router.go(-1);
        },
        jumpLink() {
            window.location.href = "https://www.bfg.capital/";
            //window.open("https:\\www.bfg.capital/","_blank")
        },
        changeisShow: function () {
            this.isShow = !this.isShow;
        },
        languageSelect(langItem) {
            //this.getLang()
            setTimeout(() => {
                console.info(langItem);
                this.langSelected = langItem;
                this.updateLang({ lang: langItem.id });
                this.$push(this.$route, true);
            }, 250);
        },
    },
};
</script>

<style lang="scss" scoped>
.header-first {
    .header-fork {
        width: 23px;
        height: 25px;
        cursor: pointer;
    }
}
.more-icon {
    width: 66px;
    height: 66px;
    margin: auto;
    margin-top: 100px;
    .more-img {
        width: 66px;
        height: 66px;
    }
}
.more-title {
    //width: 100%;
    height: 200px;
    margin: auto;
    margin-top: 248px;
    color: #2d2d2d;
    //text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    .more-text:nth-child(2) {
        // font-size: 12px;
        //padding-top: 10px;
        cursor: pointer;
        padding-top: 80px;
    }

    .more-text {
        //height: 60px;
        font-weight: 600;
        font-size: 42px;
        line-height: 60px;
        display: inline-block;
        cursor: pointer;
    }
}

.more-text-icon {
    //height: 121px;
    //width: 96px;
    color: #8a8a8a;
    text-align: center;
    padding-top: 161px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    .more-chinese {
        //width: 128px;
        height: 121px;
        display: flex;
        justify-content: space-between;
        flex-direction: row;
        align-items: center;
        cursor: pointer;

        .more-typeface {
            // width: 96px;
            font-weight: 700;
            font-size: 24px;
            line-height: 121px;
        }
        .more-left-arrow {
            width: 29px;
            padding-left: 44px;
            .more-Small-icon {
                width: 29px;
                height: 29px;
            }
        }
    }
    .more-select-lang {
        .more-Britain-english {
            width: 96px;
            font-size: 24px;
            padding-right: 35px;
            color: #8a8a8a;
            cursor: pointer;
            //display: none;
        }
    }
    .more-Britain-english:hover {
        color: #fc21ba;
    }
}
</style>


