<template>
    <div class="enterprise" v-if="width > 991">
        <ItemHeader />
        <div class="enterprise-trends">
            <div class="enterprise-div">
                <span class="enterprise-text">{{ $LS("") }}</span>
                <div class="enterprise-title">
                    <span class="enterprise-move">{{ $LS("") }}</span>
                    <span class="enterprise-move">
                        {{ $LS("Web 3.0") }}
                    </span>
                </div>
            </div>
        </div>
        <Project />
        <BottomAssembly />
    </div>
    <EnterpriseH5 v-else></EnterpriseH5>
</template>

<script>
import ItemHeader from "./items/Header.vue";
import Project from "./enterprise/Project.vue";
import BottomAssembly from "./items/BottomAssembly.vue";
import EnterpriseH5 from "./EnterpriseH5.vue";
export default {
    components: {
        ItemHeader,
        Project,
        BottomAssembly,
        EnterpriseH5,
    },
    data() {
        return {
            anchorIsShow: false,
            width: null,
        };
    },
    computed: {
        handleUpdateDimensions() {
            this.width = window.innerWidth;
        },
    },
    watch: {},
    created() {
        window.addEventListener("scroll", this.handleScrollAnchor);
        window.addEventListener("resize", this.handleUpdateDimensions);
        window.addEventListener("resize", this.handleUpdateDimensionsMethods);
    },
    mounted() {},
    methods: {
        goToLink(name) {
            this.$push({ name: name });
        },
        handleScrollAnchor() {
            if (window.pageYOffset < 300) {
                this.anchorIsShow = false;
            } else {
                this.anchorIsShow = true;
            }
        },
        handleUpdateDimensionsMethods() {
            this.width = window.innerWidth;
        },
    },
};
</script>

<style lang="scss" scoped>
@import "../styles/common";

@keyframes padding {
    from {
        padding-top: 400px;
    }

    to {
        padding-top: 142px;
    }
}

/* @mixin border-bottom {
    display: flex;
    justify-content: center;
    align-items: center;
}
@mixin Capital-BFG-Fortune {
    text-align: center;
} */
.enterprise-trends {
    min-height: 644px;
    max-height: 836px;
    .enterprise-div {
        text-align: center;
        padding-top: 142px;
        padding-bottom: 180px;
        animation: 1s padding;
        .enterprise-text {
            font-weight: 700;
            font-size: 86px;
            line-height: 90px;
            color: #000000;
            animation: 2s opacity;
        }
        .enterprise-title {
            width: 1043px;
            //height: 192px;
            margin: auto;
            padding-top: 40px;
            .enterprise-move {
                display: block;
                font-size: 33px;
                line-height: 64px;
                color: #3f3f3f;
                 animation: 2s opacity;
            }
        }
    }
}
</style>


