<template>
    <div id="navbar-header">
        <div class="content">
            <div class="header">
                <slot name="title">
                    <div class="header-icon-menu" @click="menuShow">
                        <Icon type="md-menu" size="50" />
                    </div>
                </slot>
                <img class="header-logo" :src="images.topLogo" @click="goToLink('home')" />

                <div class="header-div"></div>
            </div>
        </div>
        <div class="drawer" :class="{ 'drawer-active': menuIsShow }">
            <ul class="drawer-total">
                <li class="active" :class="{ 'active-link': homeActive }" @click="goToLink('home')">
                    <span>{{ $LS("") }}</span>
                    <!--  <Icon v-if="!homeActive" type="ios-arrow-forward"/> -->
                </li>
                <li class="active" :class="{ 'active-link': aboutActive }" @click="goToLink('about')">
                    <span>{{ $LS("") }}</span>
                    <!--   <Icon v-if="!aboutActive" type="ios-arrow-forward"/> -->
                </li>
                <li class="active" :class="{ 'active-link': developmentActive }" @click="goToLink('development')">
                    <span>{{ $LS("") }}</span>
                    <!--  <Icon v-if="!developmentActive" type="ios-arrow-forward"/> -->
                </li>
                <li class="active" :class="{ 'active-link': investmentActive }" @click="goToLink('investment')">
                    <span class="Header-text">{{ $LS("") }}</span>
                </li>
                <li class="active" :class="{ 'active-link': enterpriseActive }" @click="goToLink('enterprise')">
                    <span>{{ $LS("") }}</span>
                    <!--   <Icon v-if="!enterpriseActive" type="ios-arrow-forward"/> -->
                </li>
                <li class="active" :class="{ 'active-link': moreActive }" @click="goToLink('more')">
                    <span>{{ $LS("") }}</span>
                    <!--    <Icon v-if="!moreActive" type="ios-arrow-forward"/> -->
                </li>
            </ul>
        </div>
    </div>
</template>
<script>
import vuex from "vuex";
import { nameUtil } from "../../router/util";
import DrawerBFG from "../items/DrawerBFG.vue";

export default {
    components: {
        DrawerBFG,
    },
    data() {
        return {
            theme1: "light",
            menuIsShow: false,
            width: null,
            pathName: nameUtil.nameRemoveLanguage(this.$router.history.current.name),
            images: {
                topLogo: require("@/assets/log1234.png"),
            },

            visible: false,
            homeActive: false,
            aboutActive: false,
            developmentActive: false,
            enterpriseActive: false,
            moreActive: false,
            investmentActive: false,
        };
    },
    created() {
        window.addEventListener("scroll", this.handleScroll);
        window.addEventListener("resize", this.handleUpdateDimensionsMethods);
        if (this.pathName !== "home") {
            this.diffHeaderShow = true;
        }

        switch (this.pathName) {
            case "home":
                this.homeActive = true;
                break;
            case "about":
                this.aboutActive = true;
                break;
            case "development":
                this.developmentActive = true;
                break;
            case "enterprise":
                this.enterpriseActive = true;
                break;
            case "more":
                this.moreActive = true;
                break;
            case "investment":
                this.investmentActive = true;
                break;
        }
    },
    computed: {
        ...vuex.mapGetters(["getIsLogined"]),
        handleUpdateDimensions() {
            this.width = window.innerWidth;
        },
    },
    mounted() {},
    methods: {
        goToLink(pathName) {
            this.$push({ name: pathName });
        },
        menuShow() {
            this.menuIsShow = !this.menuIsShow;
        },
        handleUpdateDimensionsMethods() {
            this.width = window.innerWidth;
        },
    },
};
</script>
<style lang="scss" scoped>
.content {
    height: 77px;
    background-color: transparent;
    z-index: 9;
}

.headerBg {
    background-color: #f1f1f1;
}

.header {
    height: 77px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 32px;
    z-index: 99;
    background-color: #f1f1f1;

    .header-logo {
        // width: 130px;
        height: 56px;
        cursor: pointer;
    }

    .header-icon-menu {
        img {
            width: 28px;
            height: 30px;
            cursor: pointer;
        }
        /deep/ .ivu-icon {
            font-weight: 900;
            cursor: pointer;
        }
    }

    .header-div {
        width: 70px;
    }
}

.drawer {
    height: 0;
    transition: height 0.8s ease;
    overflow: hidden;
    background-color: #f1f1f1;
    .drawer-total {
        padding-top: 50px;
        font-size: 20px;
        padding: 0 50px;

        .active {
            width: 100%;
            height: 90px;
            border-bottom: 1px solid #808080;
            font-size: 24px;
            // color: #1b1d21;
            font-weight: 600;
            cursor: pointer;
            line-height: 90px;
        }
    }

    .active-link {
        span {
            font-size: 27px !important;
        }
    }
}
.drawer-active {
    height: 540px;
    transition: height 0.8s ease;
}
</style>



