import i18n from "../localized/i18n";
import VueCookie from "vue-cookie";

const languageModule = {
    namespaced: true,
    state: {
        lang: null,
    },
    mutations: {
        UPDATE_LANG(state, value) {
            state.auto_switch_dim = value;
        },


    },
    actions: {
        updateLang({ commit }, { lang, syncLocalStorage = false }) {
            let key = "language";
            if (syncLocalStorage && lang === undefined) {
                lang = VueCookie.get(key);
                if (lang && i18n.messages[lang]) {
                    i18n.locale = lang;
                }
            } else {
                VueCookie.set(key, lang);
                i18n.locale = lang;
            }
            console.info(lang,2222);
            commit("UPDATE_LANG", lang);
        },


    },
    getters: {
        getLang: state => state.lang,
    },
};
export default languageModule;



