import ApiService from "../services/api";

const serviceModule = {
    namespaced: true,
    state: {
        caseList: null
    },
    mutations: {
        CASE_LIST(state, payload) {
            state.caseList = payload;
        }
    },
    actions: {
        async getCaseListService(context) {
            let results = await ApiService.getCaseList().send();
            if (results.code === 0) {
                context.commit("CASE_LIST", results.data);
            }
            console.log(results);
        },
    },
    getters: {
        getCaseList: state => state.caseList,
    },
};
export default serviceModule;



