<template>
    <div class="dynamic">
        <p class="dynamic-f">{{ $LS("") }}</p>
        <ul class="dynamic-ul">
            <li class="dynamic-li" v-for="(item, index) in trendList" :key="index">
                <img class="dynamic-pic" :src="item.imgUrl" alt=""/>
                <div class="dynamic-li-left">
                    <span class="dynamic-name">{{ $LS(item.name) }}</span>
                    <span class="dynamic-content">{{ $LS(item.content) }}</span>
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
import { trendList } from "./dataConfig/coin.data";

export default {
    name: "Trend",
    data() {
        return {
            trendList
        };
    },
};
</script>

<style lang="scss" scoped>
@import "../../styles/common";

@keyframes padding {
    from {
        padding-top: 200px;
    }

    to {
        padding-top: 88px;
    }
}

@mixin border-bottom {
    border-top: 1px solid #c8c8c8;
}

@mixin display-flex-ai {
    display: flex;
}

@mixin display-flex-ai-jc-sb {
    @include display-flex-ai;
    justify-content: space-between;
    flex-direction: column;
}

@mixin display-flex-ai-jc-sb-da {
    @include display-flex-ai;
    align-items: center;
}

.dynamic {
    width: 100%;
    max-height: 100%;
    background-color: #ffffff;
    color: #000000;
    text-align: center;
    animation: 2s opacity;

    .dynamic-f {
        font-size: 50px;
        font-weight: 700;
        // font-size: 45px;
        padding: 88px 0 49px 0;
        animation: 1s padding;
    }

    .dynamic-ul {
        margin: 0 35px;
        padding-bottom: 104px;

        .dynamic-li {
            height: 165px;

            @include display-flex-ai-jc-sb-da;
            @include border-bottom;

            .dynamic-li:nth-child(1) {
                @include border-bottom;
            }

            .dynamic-pic {
                @include display-flex-ai;
                width: 144px;
                height: 72px;
            }

            .dynamic-li-left {
                @include display-flex-ai-jc-sb;

                .dynamic-name {
                    min-width: 200px;
                    max-width: 300px;
                    font-weight: 400;
                    font-size: 28px;
                    color: #000;
                    //padding-right: 67px;
                    text-align: left;
                }

                .dynamic-content {
                    //max-width: 340px;
                    //padding-left: 180px;
                    font-size: 24px;
                    color: #9b9b9b;
                    font-weight: 400;
                    text-align: left;
                }
            }
        }
    }
}
</style>


