<template>
    <div class="dynamic">
        <p class="dynamic-f">{{ $LS("") }}</p>
        <ul class="dynamic-ul">
            <li class="dynamic-li" v-for="(item, index) in trendList" :key="index.id">
                <div class="dynamic-li-left">
                    <img class="dynamic-pic" :src="item.imgUrl" alt="" />
                    <span class="dynamic-name">{{ $LS(item.name) }}</span>
                </div>
                <div class="dynamic-li-right">
                    <span class="dynamic-content">{{ $LS(item.content) }}</span>
                    <span class="dynamic-more">{{ $LS("") }}</span>
                </div>
            </li>
        </ul>
        <div class="button" @click="onGoToLink('development')">
            <span>{{ $LS("") }}</span>
        </div>
    </div>
</template>

<script>
import {trendList} from "./dataConfig/coin.data";

export default {
    name: "Trend",
    data() {
        return {
            // trendList: [],
            images: {
                companionB: require("../../assets/lcon.png"),
            },
            trendList,
        };
    },
    created() {
        // this.getCaseListService();
    },
    methods: {
        onGoToLink(pathName) {
            this.$push({ name: pathName });
        },
        // async getCaseListService() {
        //     // let params = {};
        //     let resulta = await ApiService.getCaseList();
        //     console.log(resulta);
        //     if (resulta.code == 0) {
        //         this.trendList=[];
        //     }else{
        //          this.trendList = resulta.data.result;
        //     }
        // },
    },
};
</script>

<style lang="scss" scoped>
@import "../../styles/common";

@keyframes padding {
    from {
        padding-top: 200px;
    }

    to {
         padding-top: 84px;

    }
  
}

@mixin border-bottom {
    border-top: 1px solid #c8c8c8;
}
@mixin display-flex-center {
    display: flex;
    justify-content: center;
    align-items: center;
}
@mixin display-flex-ai {
    display: flex;
    align-items: center;
}

@mixin display-flex-ai-jc-sb {
    @include display-flex-ai;
    justify-content: space-between;
}

.dynamic {
    width: 100%;
    max-height: 100%;
    background-color: #ffffff;
    color: #000000;
    text-align: center;
    padding-bottom: 122px;
    animation: 2s opacity;

    .dynamic-f {
        font-size: 50px;
        margin-bottom: 89px;
        padding-top: 84px;
         animation: 1s padding;
    }

    .dynamic-ul {
        margin: 0 75px;

        .dynamic-li {
            height: 135px;
            @include display-flex-ai-jc-sb;
            @include border-bottom;

            .dynamic-li:nth-child(1) {
                @include border-bottom;
            }

            .dynamic-li-left,
            .dynamic-li-right {
                @include display-flex-ai;
            }

            .dynamic-li-left {
                .dynamic-pic {
                    width: 144px;
                    height: 72px;
                }

                .dynamic-name {
                    min-width: 200px;
                    max-width: 300px;
                    font-weight: 400;
                    font-size: 24px;
                    color: #000;
                    padding-right: 67px;
                    text-align: left;
                }

                .dynamic-content {
                    font-size: 16px;
                    color: #646464;
                    font-weight: 400;
                }
            }

            .dynamic-li-right {
                .dynamic-more {
                    cursor: pointer;
                    padding-left: 120px;
                }
            }
        }
    }

    // old css
    .dynamic-list {
        width: 1290px;
        border-top: 1px solid #c8c8c8;
        margin: auto;
        display: flex;
        justify-content: space-between;
        //overflow: hidden;
        position: relative;

        .dynamic-picture {
            margin: 20px 0 43px 0;
        }

        .dynamic-text {
            position: absolute;
            width: 96px;
            height: 30px;
            left: 211px;
            top: 42px;
            font-size: 24px;
        }

        .dynamic-text1 {
            color: #646464;
            font-size: 16px;
            line-height: 24px;
            position: absolute;
            width: 320px;
            height: 24px;
            left: 756px;
            top: 45px;
        }

        .dynamic-text2 {
            color: #646464;
            font-size: 16px;
            line-height: 24px;
            position: absolute;
            width: 36px;
            height: 24px;
            left: 1250px;
            top: 45px;
        }
    }

    .button {
        width: 220px;
        height: 38px;
        margin: 0 auto;
        margin-top: 80px;
        //padding: 88px 0 122px 0;
        border: 1px solid #D7D7D7;
        background: #ebebeb;
        border-radius: 4px;
        cursor: pointer;
        @include display-flex-center;
        span {
            font-size: 16px;
            color: #000000;

        }
    }
}
</style>


