<template>
    <div class="more" v-if="width > 991">
        <!-- <ItemHeader /> -->
        <div class="more-icon" @click="upperLevel">
            <img src="../assets/large-icon.png" alt="" class="more-img" />
        </div>
        <div class="more-title">
            <span class="more-text">{{ $LS("") }}</span>
            <!-- <span class="more-text-title" @click="jumpLink">{{ $LS("bp@bfg.capital") }}</span> -->
            <span class="more-text" @click="methodLaw('statute')">{{ $LS("") }}</span>
        </div>
        <div class="more-lang">
            <div class="more-selected-warp" @click="changeisShow">
                <span class="more-selected-item">{{ langSelected.title }}</span>
                <div class="more-Small-img">
                    <img src="../assets/xiaotu.png" alt="" class="more-Small-icon" />
                </div>
            </div>
            <div class="more-select-lang" v-show="isShow">
                <span
                    class="more-lang-text"
                    v-for="(item, id) in languages"
                    :key="id"
                    v-show="langSelected.id !== item.id"
                    @click="languageSelect(item)"
                >
                    {{ item.title }}
                </span>
            </div>
        </div>
    </div>
    <MoreH5 v-else></MoreH5>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import VueCookie from "vue-cookie"
import ItemHeader from "./items/Header";
import MoreH5 from "./MoreH5.vue";
import LANG_TYPE from "../localized/language_types";

export default {
    components: {
        ItemHeader,
        MoreH5,
    },
    data() {
        let languages = [
            {id: LANG_TYPE.ZH_HANS, title: "", short: ""},
            {id: LANG_TYPE.EN, title: "English", short: "English"},
        ];
        let langType = VueCookie.get("language") || LANG_TYPE.EN;
        let defaultLangSelected = languages.find((lang) => {
            return lang.id === langType;
        });
        return {
            isShow: false,
            anchorIsShow: false,
            width: null,
            langSelected: defaultLangSelected,
            LANG_TYPE,
            languages,
        };
    },
    computed: {
        ...mapGetters("language", ["getLang"]),
        handleUpdateDimensions() {
            this.width = window.innerWidth;
        },
        currentLanguageId() {
            return this.$i18n.locale;
        },
        languagesCurrent() {
            return this.languages.find(lang => {
                return lang.id === this.currentLanguageId;
            });
        },
        languagesDropdown() {
            return this.languages.filter(lang => {
                return lang.id !== this.currentLanguageId;
            });
        },
    },
    watch: {
        // getLang:{
        //     handle(newVal,oldVal){
        //     }
        // }
    },
    created() {
        window.addEventListener("scroll", this.handleScrollAnchor);
        window.addEventListener("resize", this.handleUpdateDimensions);
        window.addEventListener("resize", this.handleUpdateDimensionsMethods);
        document.body.scrollTop ? (document.body.scrollTop = 0) : (document.documentElement.scrollTop = 0);
        
    },
    mounted() {},
    methods: {
        ...mapActions("language", ["updateLang"]),
        goToLink(name) {
            this.$push({ name: name });
        },
        methodLaw(pathName) {
            this.$push({ name: pathName });
        },
        upperLevel() {
            this.$router.go(-1); //
        },
        jumpLink() {
            window.location.href = "https://www.bfg.capital/";
            //window.open("https:\\www.bfg.capital/","_blank")
        },
        changeisShow: function () {
            this.isShow = !this.isShow;
        },
        handleScrollAnchor() {
            this.anchorIsShow = window.pageYOffset >= 300;
        },
        handleUpdateDimensionsMethods() {
            this.width = window.innerWidth;
        },
        languageSelect(langItem) {
            //this.getLang()
            setTimeout(() => {
                this.langSelected = langItem;
                this.updateLang({ lang: langItem.id });
                this.$push(this.$route, true);
            }, 250);
        },
    },
};
</script>

<style lang="scss" scoped>
@import "../styles/common";

@keyframes padding {
    from {
        margin-top: 200px;
    }

    to {
        margin-top: 100px;
    }
}

@mixin lang-warp {
    display: flex;
}

@mixin lang-text {
    font-weight: 700;
    font-size: 18px;
    cursor: pointer;
}

.more-icon {
    width: 66px;
    height: 66px;
    margin: auto;
    margin-top: 100px;
    animation: 1s padding;

    .more-img {
        width: 66px;
        height: 66px;
        cursor: pointer;
        animation: 2s opacity;
    }
}

.more-title {
    //width: 1076px;
    height: 140px;
    margin: 20px auto;
    color: #2d2d2d;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    animation: 2s opacity;

    .more-text:nth-child(2) {
        // display: none;
        padding-top: 20px;
        cursor: pointer;
    }

    .more-text {
        height: 60px;
        font-weight: 700;
        font-size: 42px;
        line-height: 60px;
        display: inline-block;
    }

    .more-text-title {
        font-size: 12px;
        padding-top: 10px;
        cursor: pointer;
    }
}

.more-lang {
    color: #8a8a8a;
    padding-top: 89px;
    display: flex;
    flex-direction: column;
    align-items: center;

    .more-selected-warp {
        @include lang-warp;

        .more-selected-item {
            @include lang-text;
            padding-right: 10px;
        }
    }

    .more-select-lang {
        @include lang-warp;
        flex-direction: column;
        align-items: center;
        animation: 2s opacity;
        padding-top: 10px;

        .more-typeface {
            @include lang-text;
        }

        .more-lang-text {
            @include lang-text;
            padding-right: 25px;
            font-weight: normal;
            color: #8a8a8a;
        }

        .more-lang-text:hover {
            color: #fc21ba;
        }
    }

    .more-Small-img {
        .more-Small-icon {
            width: 24px;
            height: 24px;
            cursor: pointer;
        }
    }
}
</style>


