import Home from '@/components/Home';
import AboutUs from '@/components/AboutUs'
import DevelopmentTrends from '@/components/DevelopmentTrends'
import Enterprise from '@/components/Enterprise'
import Investment from '@/components/Projectincubation'
import More from '@/components/More'
import Statute from '@/components/Statute'
import PageRouterView from "@/components/PageRouterView";

let one = [
    {
        path: "/",
        redirect: "BFG",
    },
    {
        path: "BFG",
        component: PageRouterView,
        children: [
            {
                path: "/",
                redirect: "home",//aboutus 
            },
            {
                path: "home",// aboutus 
                name: "home",// aboutus 
                component: Home,//AboutUs 
            },

        ],
    },
    {
        path: 'about',
        name: 'about',
        component: AboutUs,
    },
    {
        path: 'development',
        name: 'development',
        component: DevelopmentTrends,
    },
    {
        path: 'enterprise',
        name: 'enterprise',
        component: Enterprise,
    },
    {
        path: 'investment',
        name: 'investment',
        component: Investment,
    },
    {
        path: 'more',
        name: 'more',
        component: More,
    },
    {
        path: 'statute',
        name: 'statute',
        component: Statute,
    },

];

export default one



