    "StatuteH5102_220622_17H_9": "Thank you for your support of this website www.bfg.capital. Your use of the Site, including content, materials and information obtained on or through the Site (the 'Materials'), is subject to the following Terms of Use (the 'Terms of Use'). By using the Site, you are deemed to have read and agree to abide by these Terms of Use.",
    "StatuteH5103_220622_17H_10": "About Us This website is maintained and operated by BFG Capital LTD and provides operations and other services to BFG Capital entities. In accordance with industry practice, certain terms occasionally used on this site may be interpreted to mean that BFG Capital is only one entity. However, BFG Capital does not refer to a single entity. All entities under BFG Capital jointly constitute BFG Capital, and each BFG Capital entity is an independent legal entity.",
