    "StatuteH5107_220622_17H_6": "Non-offer, solicitation or suggestion none of the content on this site constitutes :(1) any form of solicitation to buy, sell, offer or buy securities or other assets and services, except as provided in the password-protected territory or as expressly stated in writing by BFG Capital; (2) Investment advice or offer of such investment advice; Or (3) the basis on which to make any investment decision. Except as expressly stated in writing by one of BFG Capital's entities, this website or the materials provided herein do not constitute a detailed or comprehensive description of BFG Capital and its investment activities.",
    "StatuteH5108_220622_17H_12": "Use restriction; We reserve all rights related to the design and content of this website. Please do not misappropriate the design and content of this website or tamper with or destroy the design and content of this website in any form. This website does not grant any license to its design or content, but you may download the materials from this website for your personal use.",
    "StatuteH5109_220622_17H_2": "Submission of Business Plans or Other Confidential Information Due to the large number of business plans and related materials we review and the similarity of many plans and materials, we cannot protect any confidential or proprietary information from disclosure or improper use without express written agreements. Any business plan, information or other material you submit on this site may be used or disclosed by us in any way we wish for any purpose. Please consider this carefully before submitting any information or material that you consider confidential or exclusive.",
