import Vue from "vue";
import VueI18n from "vue-i18n";
import LANG_TYPE from "./language_types";
import VueCookie from "vue-cookie";

Vue.use(VueI18n);

let langs = [
    [LANG_TYPE.ZH_HANS, require("./strings/zh_hans")],
    [LANG_TYPE.EN, require("./strings/en")]
];
let messages = {};
langs.forEach(obj => {
    messages[obj[0]] = obj[1];
});

let defaultLang = LANG_TYPE.EN;

const i18n = new VueI18n({
    locale: defaultLang,
    fallbackLocale: defaultLang,
    messages: messages
});

VueCookie.set("language",defaultLang)
export default i18n;



