<template>
    <div class="dynamic">
        <ul class="dynamic-ul" :class="{ 'dynamic-active': menuIsShow }">
            <li class="dynamic-li" v-for="(item, index) in trendListData" :key="index">
                <div class="dynamic-li-left">
                    <img class="dynamic-pic" :src="item.imgUrl" alt=""/>
                    <span class="dynamic-name">{{ $LS(item.name) }}</span>
                </div>
            </li>
        </ul>
<!--        <div class="button" @click="menuShow">-->
<!--            <span>{{ $LS("") }}</span>-->
<!--        </div>-->
    </div>
</template>

<script>
import {trendList} from "../items/dataConfig/coin.data";
import { arrayRemoveRepeat } from "../../util/data.util";

export default {
    name: "Dynamic",
    data() {
        return {
            menuIsShow: false,
        };
    },
    computed: {
        trendListData() {
           return arrayRemoveRepeat(trendList)
        }
    },
    methods: {
        menuShow() {
            // this.menuIsShow = !this.menuIsShow;
        },
    },
    created() {
        document.body.scrollTop ? (document.body.scrollTop = 0) : (document.documentElement.scrollTop = 0);
    },
};
</script>

<style lang="scss" scoped>
@import "../../styles/common";

@keyframes padding {
    from {
        padding-top: 300px;
    }
    to {
        padding-top: 78px;
    }
}

@mixin border-bottom {
    border-top: 1px solid #c8c8c8;
}

@mixin display-flex-center {
    display: flex;
    justify-content: center;
    align-items: center;
}

@mixin display-flex-ai {
    display: flex;
    flex-flow: row wrap;
    align-items: center;
}

@mixin display-flex-ai-jc-sb {
    @include display-flex-ai;

    justify-content: space-between;
}

.dynamic {
    // height: 740px;

    background-color: #f4f4f4;
    color: #000000;

    padding-bottom: 122px;

    .dynamic-ul {
        // width: 1600px;
        // height: 210px;
        // transition: height 0.8s ease;
        // overflow: hidden;
        margin: 0 75px;
        padding-top: 78px;
        @include display-flex-ai-jc-sb;
        animation: 1s padding;

        .dynamic-li {
            height: 135px;
            // width: 450px;
            width: 30%;
            @include display-flex-ai-jc-sb;
            @include border-bottom;
            animation: 2s opacity;

            .dynamic-li:nth-child(1) {
                @include border-bottom;
            }

            .dynamic-li-left,
            .dynamic-li-right {
                @include display-flex-ai;
            }

            .dynamic-li-left {
                .dynamic-pic {
                    width: 144px;
                    height: 72px;
                }

                .dynamic-name {
                    min-width: 100px;
                    max-width: 330px;
                    font-size: 28px;
                    color: #000;
                    //padding-right: 67px;
                    text-align: left;
                }
            }
        }
    }

    /*     .dynamic-active {
            height: 1293px;
            transition: height 0.8s ease;
        } */

    .button {
        width: 220px;
        height: 38px;
        margin: 0 auto;
        margin-top: 80px;
        //padding: 88px 0 122px 0;
        border: 1px solid #d7d7d7;
        background: #ebebeb;
        border-radius: 4px;
        cursor: pointer;
        @include display-flex-center;
        //display: none;
        span {
            font-size: 16px;
            color: #000000;
        }
    }
}
</style>


