const join = '$$$'

const nameUtil = {
  nameHasLanguage (name) {
    return this.nameFindLangeuage(name) !== null
  },
  nameAddLanguage (name, lang) {
    name = this.nameRemoveLanguage(name)
    return lang + join + name
  },
  nameFindLangeuage (name) {
    let s = name.split(join)
    if (s.length > 1) {
      let lang = s[0]
      return lang
    }
    return null
  },
  nameRemoveLanguage (name) {
    let s = name.split(join)
    if (s.length > 1) {
      return s[1]
    }
    return name
  }
}

export {
  nameUtil
}



