<template>
    <div class="development">
        <HeaderH5 />
        <div class="development-trends">
            <span class="development-text">{{ $LS("") }}</span>
            <div class="development-title">
                <!-- <span class="development-move">{{ $LS("") }}</span> -->
                <span class="development-move">{{ $LS("Web 3.0") }}</span>
            </div>
        </div>
        <DynamicH5 />
        <BottomAssemblyH5>
            <template v-slot:title>
                <div class="About-div">
                    <span class="About-span">
                        {{
                            $LS(
                                "BFG CapitalBittrexOkexCoinbaseFTXBybitCoinoneBithumbKucoin Gate.io MXCBitrueLbank"
                            )
                        }}
                    </span>
                    <span class="About-span">
                        {{ $LS("") }}
                    </span>
                     <span class="About-span send-email-h5">{{$LS("listing@bfg.capital")}}</span>
                </div>
            </template>
        </BottomAssemblyH5>
    </div>
</template>

<script>
import HeaderH5 from "./items/HeaderH5.vue";
import DynamicH5 from "./development/dynamicH5.vue";
import BottomAssemblyH5 from "./items/BottomAssemblyH5.vue";
import "../common/px";
export default {
    components: {
        HeaderH5,
        DynamicH5,
        BottomAssemblyH5,
    },
    data() {
        return {};
    },
};
</script>

<style lang="scss" scoped>
@import "../styles/common";

@keyframes padding {
    from {
        padding-top: 400px;
    }
    to {
        padding-top: 169px;
    }
}

@mixin border-bottom {
    display: flex;
    justify-content: center;
    align-items: center;
}
@mixin Capital-BFG-Fortune {
    text-align: center;
}
.development-trends {
    min-height: 564px;
    max-height: 692px;
    /*  @include border-bottom; */
    text-align: center;
    padding-top: 169px;
    padding-bottom: 149px;
    animation: 1s padding;
    .development-text {
        font-weight: 600;
        font-size: 75px;
        line-height: 90px;
        color: #000000;
        animation: 2s opacity;
    }
    .development-title {
        // width: 665px;
        padding: 28px 20px 0 20px;
        .development-move {
            display: block;
            font-size: 33px;
            line-height: 64px;
            color: #3f3f3f;
            animation: 2s opacity;
        }
    }
}
.About-div {
    display: flex;
    flex-direction: column;
    .About-span {
        font-size: 20px;
        line-height: 48px;
        color: #ffffff;
        font-weight: 200;
    }
    .send-email-h5{
        font-weight: bold;
    }
}
</style>


