<template>
    <div class="development" v-if="width > 991">
        <ItemHeader />
        <div class="development-trends">
            <span class="development-text">{{ $LS("") }}</span>
            <div class="development-title">
                <!-- <span class="development-move">{{ $LS("") }}</span> -->
                <span class="development-move">{{ $LS("Web 3.0") }}</span>
                <!--  <span class="development-move">{{ $LS("") }}</span> -->
            </div>
        </div>
        <Dynamic />
        <BottomAssembly>
            <template v-slot:title>
                <div class="About-div">
                    <span class="About-span">
                        {{
                            $LS(
                                "BFG CapitalBittrexOkexCoinbaseFTXBybitCoinoneBithumbKucoin Gate.io MXCBitrueLbank"
                            )
                        }}
                    </span>
                    <span class="About-span send-email">{{ $LS("listing@bfg.capital") }}</span>
                </div>
            </template>
        </BottomAssembly>
    </div>
    <DevelopmentTrendsH5 v-else></DevelopmentTrendsH5>
</template>

<script>
import ItemHeader from "./items/Header.vue";
import Dynamic from "./development/dynamic.vue";
import BottomAssembly from "./items/BottomAssembly.vue";
import DevelopmentTrendsH5 from "./DevelopmentTrendsH5.vue";
export default {
    components: {
        ItemHeader,
        Dynamic,
        BottomAssembly,
        DevelopmentTrendsH5,
    },
    data() {
        return {
            anchorIsShow: false,
            width: null,
        };
    },
    computed: {
        handleUpdateDimensions() {
            this.width = window.innerWidth;
        },
    },
    watch: {},
    created() {
        window.addEventListener("scroll", this.handleScrollAnchor);
        window.addEventListener("resize", this.handleUpdateDimensions);
        window.addEventListener("resize", this.handleUpdateDimensionsMethods);
    },
    mounted() {},
    methods: {
        // goToLink(name) {
        //     this.$push({name: name});
        // },
        handleScrollAnchor() {
            if (window.pageYOffset < 300) {
                this.anchorIsShow = false;
            } else {
                this.anchorIsShow = true;
            }
        },
        handleUpdateDimensionsMethods() {
            this.width = window.innerWidth;
        },
    },
};
</script>

<style lang="scss" scoped>
@import "../styles/common";

@keyframes padding {
    from {
        padding-top: 400px;
    }
    to {
        padding-top: 174px;
    }
}

@mixin border-bottom {
    display: flex;
    justify-content: center;
    align-items: center;
}
@mixin Capital-BFG-Fortune {
    text-align: center;
}
.development-trends {
    /*  @include border-bottom; */
    height: 570px;
    text-align: center;
    padding-top: 174px;
    padding-bottom: 138px;
    animation: 1s padding;

    .development-text {
        font-weight: 600;
        font-size: 86px;
        line-height: 90px;
        color: #000000;
        animation: 2s opacity;
    }
    .development-title {
        padding-top: 40px;
        .development-move {
            display: block;
            font-size: 33px;
            line-height: 64px;
            color: #3f3f3f;
            animation: 2s opacity;
        }
    }
}
.About-div {
    .About-span {
        font-size: 17px;
        line-height: 32px;
        color: #ffffff;
        font-weight: 200;
    }
    .send-email {
        font-weight: bold;
    }
}
</style>


