import i18n from "./i18n.js";
import LANG_TYPE from "./language_types";

const cn = i18n.messages[LANG_TYPE.ZH_HANS];

function _reflection(obj, keyPre = "") {
    var result = {};
    for (var key in obj) {
        if (obj.hasOwnProperty(key)) {
            var value = obj[key];
            // console.log(key, value, typeof value)
            if (typeof value === "string") {
                result[value] = keyPre + key;
                continue;
            }
            let r2 = _reflection(value, keyPre + key + ".");
            result = {...result, ...r2};
        }
    }
    return result;
}

const cnKeysReflection = _reflection(cn);

// console.log(cnKeysReflection)

function localizedString(keyCN, locale, values) {
    let key = cnKeysReflection[keyCN];
    // console.log(key)
    if (!key) {
        return keyCN;
    }
    let msg = i18n.t(key, locale, values);
    // console.log(msg)
    return msg;
}

export default localizedString;



