import Vue from "vue";
import Router from "vue-router";

import PageRouterView from "@/components/PageRouterView";

import LANG_TYPE from "@/localized/language_types";
import one from "./routes";

import {nameUtil} from "@/router/util.js";

Vue.use(Router);

let langs = Object.values(LANG_TYPE);

// console.log(langs)

function changeRouter(routers, lang) {
    let rs = [...routers];
    rs = rs.map(obj => {
        let newObj = {...obj};
        let name = newObj.name;
        if (name) {
            newObj.name = nameUtil.nameAddLanguage(name, lang);
        }
        let children = newObj.children;
        if (children) {
            newObj.children = changeRouter(children, lang);
        }
        return newObj;
    });
    return rs;
}

let rLang = langs.map(lang => {
    let children = changeRouter(one, lang);
    return {
        path: "/",
        component: PageRouterView,
        children: children,
    };
});

let router = new Router({
    routes: [
        {
            path: "/home",
        },
        {
            path: "*",
            redirect: "/home",
        },
        ...rLang,
    ],
});
const originalPush = Router.prototype.push;
const originalReplace = Router.prototype.replace;
Router.prototype.push = function push(location) {
    return originalPush.call(this, location).catch(err => err);
};
Router.prototype.replace = function replace(location) {
    return originalReplace.call(this, location).catch(err => err);
};
export default router;



