<template>
    <div id="navbar-header">
        <div class="content">
            <div class="header" :class="{ headerBg: headerIsFix }">
                <!-- logo -->
                <!--  <ProjectHeader v-if="diffHeaderShow && headerIsFix"></ProjectHeader> -->
                <img class="header-logo" :src="images.topLogo" @click="goToLink('home')" />

                <div class="Head-navigation">
                    <div class="Head-jump" :class="{ 'active-link': aboutActive }" @click="goToLink('about')">
                        <span class="Header-text">{{ $LS("") }}</span>
                    </div>
                    <div
                        class="Head-jump"
                        :class="{ 'active-link': developmentActive }"
                        @click="goToLink('development')"
                    >
                        <span class="Header-text">{{ $LS("") }}</span>
                    </div>
                    <div class="Head-jump" :class="{ 'active-link': investmentActive }" @click="goToLink('investment')">
                        <span class="Header-text">{{ $LS("") }}</span>
                    </div>
                    <div class="Head-jump" :class="{ 'active-link': enterpriseActive }" @click="goToLink('enterprise')">
                        <span class="Header-text">{{ $LS("") }}</span>
                    </div>

                    <div class="header-icon-menu" @click="goToLink('more')">
                        <!-- <img src="../../assets/menus.png" alt=""> -->
                        <Icon type="md-menu" size="20" />
                    </div>
                </div>
            </div>
        </div>
        <!-- <ItemDrawer
          :menuShowValue="menuIsShow"
          @menuByValue="menuByValue"
        ></ItemDrawer> -->
    </div>
</template>
<script>
import vuex from "vuex";
import { nameUtil } from "../../router/util";

export default {
    components: {
        //ItemDrawer,
        //ProjectHeader,
    },
    /*  props: {
        menuValue2: null,
    }, */
    data() {
        return {
            theme1: "light",
            menuIsShow: false,
            headerIsFix: false,
            diffHeaderShow: false,
            width: null,
            pathName: nameUtil.nameRemoveLanguage(this.$router.history.current.name),
            images: {
                /*         topLogoW: require("@/assets/topLogoW.png"), */
                topLogo: require("@/assets/log1234.png"),
            },
            aboutActive: false,
            developmentActive: false,
            enterpriseActive: false,
            moreActive: false,
            investmentActive: false,
        };
    },
    created() {
        window.addEventListener("scroll", this.handleScroll);
        window.addEventListener("resize", this.handleUpdateDimensionsMethods);
        if (this.pathName !== "home") {
            this.diffHeaderShow = true;
        }
        switch (this.pathName) {
            case "about":
                this.aboutActive = true;
                break;
            case "development":
                this.developmentActive = true;
                break;
            case "enterprise":
                this.enterpriseActive = true;
                break;
            case "more":
                this.moreActive = true;
                break;
            case "investment":
                this.investmentActive = true;
                break;
        }
    },
    computed: {
        ...vuex.mapGetters(["getIsLogined"]),
        handleUpdateDimensions() {
            this.width = window.innerWidth;
        },
    },
    /* watch: {
        menuValue2(curVal, oldVal) {
            this.menuIsShow = curVal;
        }, 
    },*/
    mounted() {},
    methods: {
        goToLink(pathName) {
            this.$push({name: pathName});
        },
        menuByValue(value) {
            this.menuIsShow = value;
        },
        handleScroll() {
            this.headerIsFix = window.pageYOffset > 49;
        },
        handleUpdateDimensionsMethods() {
            this.width = window.innerWidth;
        },
    },
};
</script>
<style lang="scss" scoped>
.content {
    width: 100%;
    height: 72px;
    margin: 0 auto;
    position: fixed;
    left: 0;
    top: 0;
    background-color: transparent;
    z-index: 9;
}

.headerBg {
    background-color: #f1f1f1;
}

.header {
    height: 73px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 19px;
    z-index: 99;
    background-color: #f1f1f1;

    .header-logo {
        height: 42px;
        margin-right: 90px;
        cursor: pointer;
    }

    .header-icon-menu {
        img {
            width: 20px;
            height: 20px;
            cursor: pointer;
        }
        /deep/ .ivu-icon {
            font-weight: 900;
            cursor: pointer;
        }
    }

    .Head-navigation {
        width: 394px;
        display: flex;
        justify-content: space-between;
        align-items: center;

        .Head-jump {
            color: #000000;
            cursor: pointer;

            .Header-text {
                font-size: 12px;
            }
        }

        .active-link {
            span {
                font-size: 14px !important;
            }
        }
    }
}
</style>



