        </ul>
    </div>
</template>

<script>
export default {
    name: "AnchorItemH5",
    props: {
        /* list: {
            type: Array,
            default: [],
        }, */
    },
    data() {
        return {
            list: [
                {
                    id: 1,
                    imgUrl: require("../../assets/coinbase-logo@2x.png"),
                },
                {
                    id: 2,
                    imgUrl: require("../../assets/coingecko@2x.png"),
                },
                {
                    id: 3,
                    imgUrl: require("../../assets/coinmarketcap-logo@2x.png"),
                },
                {
                    id: 4,
                    imgUrl: require("../../assets/coinone@2x.png"),
                },
                {
                    id: 5,
                    imgUrl: require("../../assets/bittre@2x.png"),
                },
                {
                    id: 6,
                    imgUrl: require("../../assets/mexc@2x.png"),
                },
                {
                    id: 7,
                    imgUrl: require("../../assets/huobi@2x.png"),
                },
                {
                    id: 8,
                    imgUrl: require("../../assets/kucoin_logo@2x.png"),
                },
                {
                    id: 9,
                    imgUrl: require("../../assets/kraken@2x.png"),
                },
                {
                    id: 10,
                    imgUrl: require("../../assets/Crypto.png"),
                },
                {
                    id: 11,
                    imgUrl: require("../../assets/Poloniex.png"),
                },

            ],
