<style lang="scss" scoped>
@import "../../styles/common";

@keyframes padding {
    from {
        padding-top: 200px;
    }

    to {
        padding-top: 88px;
    }
}

.cooperative-partner {
    width: 100%;
    min-height: 796px;
    max-height: 1063px;
    background-color: #131313;
    background-image: url(../../assets/Black-map.png);
    background-repeat: no-repeat;
    //background-size: 582px 388px;
    background-position: 50% 100%;

    .partner-title {
        padding-top: 88px;
        animation: 1s padding;

        .partner {
            text-align: center;
            font-weight: 400;
            padding-bottom: 118px;
            font-size: 50px;
            color: #ffffff;
            animation: 2s opacity;
        }
    }

    .cooperative-Alipay {
        display: grid;
        grid-template-columns: 40% 40% 0;
        grid-template-rows: 112px 112px;
        padding-bottom: 198px;
        margin: 0 87px;

        .cooperative-Alipay-I {
            //margin-right: 144px;
            .cooperative-Alipay-II {
                width: 110px;
                height: 70px;
                // width: 96px;
                // height: 32px;
            }
        }
    }
}
</style>


