<template>
    <div class="home">
        <!-- v-if="(width>991)" -->
        <!--  <Affix> -->
        <HeaderH5></HeaderH5>
        <!--  </Affix> -->
        <div>
            <div class="home-page-banner">
                <div class="home-page-text">
                    <span class="home-page-p">
                        <i class="home-i">{{ $LS("") }}</i>
                        {{ $LS("") }}
                    </span>
                    <span class="home-page-p">
                        <i class="home-i">{{ $LS("") }}</i>
                        {{ $LS("Web 3.0") }}
                    </span>
                    <span class="home-page-p">
                        {{ $LS("") }}
                        <i class="home-i">{{ $LS("") }}</i>
                    </span>
                </div>
                <div class="home-page-text-bottom">
                    <img :src="images.companionBg" alt="" class="home-page-logo" />
                    <!-- <p class="home-page-xp">{{ $LS("") }}</p> -->
                    <!-- <img src="../assets/xiaotu.png" alt="" class="ivu-icon"/> -->
                </div>
            </div>
            <RotationH5 />
            <TrendH5 />
            <InvestmentIncubationH5 />
            <PartnersH5 />
            <!-- :list="listImg" -->
            <BottomAssemblyH5 />
        </div>
    </div>
    <!-- <HomeH5 v-else></HomeH5> -->
</template>

<script>
import HeaderH5 from "./items/HeaderH5";

import RotationH5 from "./items/RotationH5.vue";
import TrendH5 from "./items/TrendH5.vue";
import InvestmentIncubationH5 from "./items/InvestmentIncubationH5.vue";
import PartnersH5 from "./items/PartnersH5.vue";
import BottomAssemblyH5 from "./items/BottomAssemblyH5.vue";
import "../common/px";

// import ItemProjectHeader from "./items/ProjectHeader";
// import HomeH5 from "./HomeH5";

export default {
    components: {
        HeaderH5,
        RotationH5,
        TrendH5,
        InvestmentIncubationH5,
        PartnersH5,
        BottomAssemblyH5,
        /* HomeH5, */
    },
    props: {},
    data() {
        return {
            anchorIsShow: false,
            width: null,
            images: {
                companionBg: require("../assets/log1234.png"),
            },
            /* listImg: [0, 1, 2, 3, 4, 5, 6, 7, 8], */
        };
    },
    computed: {
        handleUpdateDimensions() {
            this.width = window.innerWidth;
        },
    },
    watch: {},
    created() {
        window.addEventListener("scroll", this.handleScrollAnchor);
        window.addEventListener("resize", this.handleUpdateDimensions);
        window.addEventListener("resize", this.handleUpdateDimensionsMethods);
    },
    mounted() {},
    methods: {
        goToLink(name) {
            this.$push({ name: name });
        },
        handleScrollAnchor() {
            if (window.pageYOffset < 300) {
                this.anchorIsShow = false;
            } else {
                this.anchorIsShow = true;
            }
        },
        handleUpdateDimensionsMethods() {
            this.width = window.innerWidth;
        },
    },
};
</script>
<style lang="scss" scoped>
// @import "../styles/home";

@import "../styles/common";

@keyframes padding {
    from {
        padding-top: 400px;
    }

    to {
        padding-top: 214px;
    }
}

.home {
    .home-page-banner {
        height: 1200px;
        text-align: center;
        background-color: #f1f1f1;
        background-image: url("../assets/banner.png");
        background-size: 616px 121px;
        //background-position: center 988px;
        background-position: center 100%;
        background-repeat: no-repeat;
        padding-top: 214px;
        animation: 1s padding;

        .home-page-text {
            display: flex;
            flex-direction: column;
            .home-page-p {
                font-size: 48px;
                color: #2d2d2d;
                line-height: 100px;
                font-weight: 200;
                animation: 2s opacity;
                  .home-i{
                      font-weight: 700;
                  }
            }
        }

        .home-page-text-bottom {
            padding-top: 126px;
            .home-page-logo {
                width: 291px;
                height: 125px;
                animation: 2s opacity;
            }
        }
    }
}
</style>



