                            {{ $LS("") }}
                        </p>
                    </slot>
                    <div class="Button" @click="Twitter">
                        <Button class="button-map">
                            <img src="../../assets/Bird-map.png" alt="" class="Bird-map" />
                            <span class="Twitter">{{ $LS("Twitter") }}</span>
                        </Button>
                    </div>
                    <div class="button-law">
<!--                        <span class="button-law-statement">{{ $LS("") }}</span>-->
                    </div>
                </div>
            </div>
            <div class="Clause-statement">
