            </div>
            <div class="bottom-arrow" @click="topList">
                <img src="../../assets/arrow.png" alt="" class="arrow" />
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "BottomAssemblyH5",
    data() {
        return {};
    },
    methods: {
        Twitter() {
            // window.location.href = "https://twitter.com/robinluo0920/";
            window.open("https://twitter.com/robinluo0920/", "_blank");
        },
        topList() {
            document.documentElement.scrollTop = 0;
        },
    },
};
</script>

<style lang="scss" scoped>
@import "../../styles/common";

@keyframes padding {
    from {
        padding-top: 200px;
    }

    to {
        padding-top: 54px;
    }
}

.Bottom-assembly {
    width: 100%;
    // height: 870px;
    background: #212121;
    display: inline-block;
    color: #ffffff;
    padding-top: 54px;
    animation: 1s padding;

    .Bottom-assembly-L {
        margin: 0 38px;
        animation: 2s opacity;
        .Bottom-xo {
            width: 278px;
            height: 48px;
        }
        .Bottom-contactUs {
            font-size: 24px;
            line-height: 48px;
            opacity: 0.5;
        }
        .Horizontal-line {
            height: 360px;
            // max-height: 540px;
            border-top: 1px solid #3e3e3e;
            border-bottom: 1px solid #3e3e3e;
            margin: 12px 0;
            .Bottom-text {
                padding-top: 20px;
                .Bottom-Jump {
                    height: 88px;
                    // max-height: 540px;
                    font-size: 24px;
                    line-height: 44px;
                    .Bottom-link {
                        color: #ffffff;
                        border-bottom: 1px solid #ffffff;
                    }
                }
                .Button {
                    width: 220px;
                    height: 38px;
                    margin-top: 52px;

                    .button-map {
                        width: 100%;
                        height: 38px;
                        background-color: #4989d3;
                        // border-radius: 60px;
                        color: #ffffff;
                        border-color: #262626;
                        box-shadow: none;
                        .Bird-map {
                            width: 18px;
                            height: 14px;
                            margin-right: 18px;
                            margin-bottom: 8px;
                        }
                        .Twitter {
                            width: 55px;
                            height: 19px;
                            font-size: 16px;
                            padding-left: 19px;
                        }
                    }
                }
                .button-law {
                    height: 36px;
                    color: #ffffff;
                    opacity: 0.5;
                    padding-top: 13px;
                    .button-law-statement {
