    .Clause-statement {
        display: flex;
        justify-content: flex-start;
        flex-direction: column;
        color: #ffffff;
        font-size: 24px;
        line-height: 36px;
        //display: none;
        .Bottom-Clausestatement,
        .Clausestatement {
            // display: none;
        }
        .Bottom-Clausestatement {
            width: 278px;
            height: 36px;
            padding: 48px 0;
        }
        .Clausestatement {
            width: 278px;
            height: 36px;
            padding: 42px 0;
        }
    }
    .bottom-BFG-Company {
        width: 590px;
        height: 36px;
        margin-top: 60px;
        /* background-image: url(../../assets/BFG-2022.png);
        background-repeat: no-repeat;
        background-position: 50% 50%;
        background-size: 80% 80%; */
        .bottom-BFG {
            font-weight: 400;
            font-size: 24px;
            line-height: 36px;
            color: #ffffff;
            opacity: 0.5;
        }
    }
    .bottom-arrow {
        margin: 65px 0 40px 0;
        text-align: center;
        .arrow {
            width: 37px;
            height: 16px;
        }
    }
}
</style>


