<script>
export default {
    name: "InvestmentIncubation",
    data() {
        return {
            images: {
                icons: require("../../assets/small-icons.png"),
                icons1: require("../../assets/small-icons1.png"),
                icons2: require("../../assets/small-icons2.png"),
                icons3: require("../../assets/small-icons3.png"),
                icons4: require("../../assets/small-icons4.png"),
                icons5: require("../../assets/small-icons4.png"),
