        display: flex;
        justify-content: center;
        align-items: center;
        padding-top: 158px;
        animation: 1s paddingTop;
        min-width: 1300px;
        .investment-li {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            animation: 2s opacity;
            width: 240px;

            img {
                width: 60px;
                height: 60px;
            }

            span {
                padding-top: 44px;
                font-size: 20px;
               
            }
        }
        .investment-li:nth-child(2){
            img{
                height: 56px;
            }
            //padding: 0 140px;
        }
        .investment-li:nth-child(4){
            img{
                height: 56px;
            }
            //padding: 0 140px;
        }
