<template>
    <div class="rotation">
        <Carousel autoplay v-model="value" radius-dot arrow="never" :autoplay-speed=3000 loop>
            <CarouselItem v-for="(item, index) in rotationList" :key="index">
                <div
                    :height="640"
                    class="demo-carousel"
                    :style="{ 'background-image': 'url(' + item.imgUrl + ')' }"
                ></div>
            </CarouselItem>
        </Carousel>
        <div class="rotation-more" @click="goToLink('enterprise')">
            <span>{{ $LS("") }}</span>
        </div>
    </div>
</template>

<script>
export default {
    name: "Rotation",
    data() {
        return {
            value: 0, // 
            rotationList: [
                {
                    id: 0.5,
                    imgUrl: require("../../assets/sui.jpg"),
                },
                {
                    id: 1,
                    imgUrl: require("../../assets/bitcoin.png"),
                },
                {
                    id: 2,
                    imgUrl: require("../../assets/ethereum.png"),
                },
                {
                    id: 3,
                    imgUrl: require("../../assets/bnb.png"),
                },
                {
                    id: 4,
                    imgUrl: require("../../assets/dogecoin.png"),
                },
                {
                    id: 5,
                    imgUrl: require("../../assets/solana.png"),
                },
                {
                    id: 6,
                    imgUrl: require("../../assets/avalanche.png"),
                },
                {
                    id: 7,
                    imgUrl: require("../../assets/sandbox.png"),
                },
            ],
        };
    },
    methods: {
        goToLink(pathName) {
            this.$push({ name: pathName });
        },
    },
};
</script>

<style lang="scss" scoped>
.rotation {
    width: 100%;
    color: #ffffff;
    position: relative;
    .demo-carousel {
        width: 100%;
        height: 640px;
        background-position: 50% 50%;
    }

    /deep/ .ivu-carousel {
        .ivu-carousel-track {
        }
        .ivu-carousel-item {
            -webkit-transform: translate3d(0, 0, 0);
        }

        .ivu-carousel-dots-inside {
            //float: left;
            width: 8px;
            height: 88px;
            position: absolute;
            left: 33px;
            top: 50%;
            margin-top: -44px;
        }

        .ivu-carousel-dots li button {
            background-color: #111111;
        }
    }

    .rotation-more {
        //width: 72px;
        height: 30px;
        font-size: 12px;
        border: 1px solid rgba(4, 4, 4, 0.15);
        position: absolute;
        right: 38px;
        top: 50%;
        margin-top: -15px;
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;
        opacity: 0.4;
        padding: 0 4px;
    }
}
</style>


