const axios = require("axios");

let logEnable = false;
const methods = {
    GET: "GET",
    POST: "POST",
    PUT: "PUT",
    PATCH: "PATCH",
    DELETE: "DELETE",
};

axios.defaults.withCredentials = true;
const _axios = axios.create({
    //baseURL: BASE_URL,
    timeout: 180 * 1000, // *1000
    headers: {
        "Content-Type": "application/json",
    },
    withCredentials: true,
});

let AJAX_SEND_COUNT = 0;

let getBaseUrl = buildQueryString({url: window.api_host});
let testUrl = "http://www.bfg.capital";

class HttpParams {
    constructor(
        init = {
            baseUrl: "",
            action: "",
            params: {},
            method: methods.GET,
            mark: "",
            headers: {},
            withCredentials: true,
            retry: 0,
            responseHeaders: false,
            isDownload: false,
        }
    ) {
        this.baseUrl = init.baseUrl || getBaseUrl || testUrl;
        this.action = init.action;
        this.params = init.params;
        this.method = init.method;
        this.mark = init.mark;
        this.headers = init.headers;
        this.withCredentials = init.withCredentials;
        this.retry = init.retry;
        this.responseHeaders = init.responseHeaders;
        this.isDownload = init.isDownload;
    }
    
    async send() {
        try {
            return (await http(this)).data;
        } catch (error) {
            if (error.response) {
                return error.response.data;
            }
            throw error;
        }
    }
}

/**
 *
 * @param {HttpParams} httpParams
 * @param {*} callback
 * @returns
 */
let baseHttp = async function (httpParams, callback) {
    // see: https://www.kancloud.cn/yunye/axios/234845
    let {
        baseUrl,
        action,
        params,
        method,
        mark,
        headers,
        withCredentials,
        retry,
        responseHeaders,
        isDownload,
    } = httpParams;
    AJAX_SEND_COUNT += 1;
    let sendNumber = AJAX_SEND_COUNT;
    let logRequestPrefix = `(${sendNumber}) => ${mark || ""} ${method} ${action}`;
    let sendConfig = {
        baseURL: baseUrl || "http://localhost:6010",
        url: action,
        method,
        withCredentials,
    };
    if (isDownload) {
        sendConfig.responseType = "blob";
    }
    if (headers) {
        sendConfig.headers = {
            ...headers,
        };
    }
    if (method === methods.GET || method === methods.DELETE) {
        sendConfig.params = params;
    } else {
        sendConfig.data = params;
    }
    if (logEnable) {
        console.log("REQUEST " + logRequestPrefix, sendConfig);
    }
    let promise = new Promise((resolve, reject) => {
        _axios(sendConfig)
            .then(response => {
                if (logEnable) {
                    console.log("RESPONSE SUCCESS " + logRequestPrefix, response);
                }
                if (responseHeaders) {
                    return resolve(response.data);
                }
                resolve(response);
            })
            .catch(async err => {
                // 
                if (logEnable) {
                    console.log("RESPONSE ERROR " + logRequestPrefix, err);
                }
                reject(err);
            });
    });
    if (!callback) {
        return promise;
    }
    promise
        .then(response => {
            callback(response, null);
        })
        .catch(err => {
            callback(null, err);
        });
};

function buildQueryString({url, action, params} = {}) {
    let merge = [];
    if (url) {
        merge.push(url);
    }
    if (url && action) {
        if (!url.endsWith("/") && !action.startsWith("/")) {
            merge.push("/");
        } else if (url.endsWith("/") && action.startsWith("/")) {
            action = action.substring(1);
        }
    }
    if (action) {
        merge.push(action);
    }
    let esc = encodeURIComponent;
    let query;
    if (params) {
        let qs = [];
        Object.keys(params).forEach(k => {
            if (params[k]) {
                qs.push(esc(k) + "=" + esc(params[k]));
            }
        });
        query = qs.join("&");
    }
    if (query) {
        if (merge.length > 0) {
            merge.push("?");
        }
        merge.push(query);
    }
    return merge.join("");
}

// export { methods, buildQueryString, baseHttp, _removeQueue };

/**
 *
 * @param {HttpParams} urlApiConfig
 * @param {*} callback
 * @returns
 */
let http = (urlApiConfig, callback) => {
    let config = {
        ...urlApiConfig,
        withCredentials: true,
    };
    config.headers = {
        ...config.headers,
    };
    return baseHttp(config, callback);
};
// module.exports = {
//     HTTP: http,
//     HTTP_METHOD: methods,
//     HttpParams,
// };
export {
    http,
    methods,
    HttpParams,
};


