<template>
    <div class="Bottom-assembly">
        <div class="Bottom-assembly-L">
            <div class="Bottom-xo">
                <span class="Bottom-contactUs">{{ $LS("") }}</span>
            </div>
            <div class="Horizontal-line"></div>
            <div class="Bottom-text">
                <slot name="title">
                    <p class="Bottom-Jump">
                        {{ $LS("") }}
                        <span class="Bottom-link">
                            {{ $LS("bp@bfg.capital") }}
                        </span>
                        {{ $LS("") }}
                    </p>
                </slot>

                <div class="Button" @click="Twitter">
                    <Button class="button-map">
                        <img src="../../assets/Bird-map.png" alt="" class="Bird-map" />
                        <span class="Twitter">{{ $LS("Twitter") }}</span>
                    </Button>
                </div>
            </div>
            <div class="Clause-statement">
                <a href="" class="Clausestatement">{{ $LS("") }}</a>
                <!--  <a href="" class="Clausestatement">{{ $LS("") }}</a> -->
            </div>
            <div class="bottom-BFG"></div>
        </div>
    </div>
</template>

<script>
export default {
    name: "BottomAssembly",
    data() {
        return {};
    },
    methods: {
        Twitter() {
            // window.location.href = "https://twitter.com/robinluo0920/";
            window.open("https://twitter.com/robinluo0920/", "_blank");
        },
    },
};
</script>

<style lang="scss" scoped>
@import "../../styles/common";

@keyframes padding {
    from {
        padding-top: 200px;
    }

    to {
        padding-top: 48px;
    }
}
.Bottom-assembly {
    width: 100%;
    height: 455px;
    background: #1a1a1a;
    display: inline-block;
    color: #ffffff;
    padding-top: 48px;
    animation: 1s padding;
    .Bottom-assembly-L {
        // width: 542px;
        width: 100%;
        height: 359px;
        margin: 0 auto;
        animation: 2s opacity;

        /* border: 1px solid red; */
        .Bottom-xo {
            width: 278px;
            height: 48px;
            margin: 0 auto;
            text-align: center;

            .Bottom-contactUs {
                font-size: 32px;
                opacity: 0.5;
            }
        }

        .Horizontal-line {
            // border-top: 1px solid #3e3e3e;
            margin: 20px 0 20px 0;
        }
        .Bottom-text {
            width: 1100px;
            height: 221px;
            margin: 0 auto;
            // height: 259px;
            text-align: center;
            .Bottom-Jump {
                font-size: 16px;
                line-height: 28px;
                .Bottom-link {
                    color: #ffffff;
                    border-bottom: 1px solid #ffffff;
                }
            }
            .Button {
                width: 220px;
                height: 38px;
                margin: auto;
                padding-top: 20px;

                .button-map {
                    width: 100%;
                    height: 38px;
                    background-color: #4989d3;
                    color: #ffffff;
                    border-radius: 4px;
                    border-color: #262626;

                    box-shadow: none;
                    .Bird-map {
                        width: 18px;
                        height: 14px;
                        margin-right: 18px;
                        margin-bottom: 8px;
                    }
                    .Twitter {
                        width: 55px;
                        height: 19px;
                        font-size: 16px;
                        padding-left: 18px;
                    }
                }
            }
        }
    }
    .Clause-statement {
        width: 172px;
        height: 19px;
        margin: auto;
        /*  border: 1px solid red; */
        display: flex;
        justify-content: center;
        // padding: 0 3px 0 3px;
        line-height: 20px;
        // padding-top: 18%;
        text-align: center;

        .Clausestatement {
            color: #ffffff;
            font-size: 14px;
            // display: none;
        }
    }
    .bottom-BFG {
        width: 356px;
        height: 19px;
        margin: auto;
        margin-top: 31px;
        background-image: url(../../assets/BFG-2022.png);
        background-repeat: no-repeat;
        background-position: 50% 50%;
        background-size: 80% 80%;
    }
}
</style>


