import Vue from "vue";

// iView

import ViewUI from "view-design";
import "view-design/dist/styles/iview.css";

// 
import VueI18n from "vue-i18n";
import i18n from "./localized/i18n";

import App from "./App";
import router from "./router";

// vuex
import store from "./store";

// 
import LS from "./localized/localized.js";

import { nameUtil } from "./router/util.js";

Vue.use(ViewUI);
Vue.use(VueI18n);

Vue.config.productionTip = false;

Vue.prototype.$LS = LS;

Vue.prototype.$push = function (payload, changeLang = false) {
    payload = {...payload};
    if (changeLang) {
        if (payload.name) {
            let name = nameUtil.nameRemoveLanguage(payload.name);
            payload.name = name;
        }
    }
    let name = nameUtil.nameAddLanguage(payload.name, i18n.locale);
    payload.name = name;
    // console.log(payload)
    router.push(payload);
};

/* eslint-disable no-new */
window._app = new Vue({
    el: "#app",
    store,
    i18n,
    router,
    components: {App},
    template: "<App/>"
});



